@import url("https://fonts.googleapis.com/css2?family=Big+Shoulders+Stencil+Text:wght@500&display=swap");

.random-container {
    background: #383838;
    width: 75%;
    height: 250px;
    border-radius: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    filter: drop-shadow(2px 15px 15px rgb(17, 17, 17));
    margin: 25px 0;
    flex-direction: column;
}
.choose-btn {
    padding: 10px;
    border-radius: 55px;
    width: 25%;
    color: rgb(255, 255, 255);
    background: black;
    border: 1px solid white;
    min-width: 55px;
    filter: drop-shadow(2px 7px 5px rgb(17, 17, 17));
    margin-top: 40px;
    cursor: pointer;
    font-size: 1.2em;
    letter-spacing: 0.1em;
}
.choose-btn:focus{
    outline: none;
}


.chosen-view {
    background: rgba(255, 255, 255, 0);
    width: 70%;
    height: 25%;
    color: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    filter: drop-shadow(5px 25px 1px rgb(17, 17, 17));
    font-size: 1.5em;
    font-family: "Big Shoulders Stencil Text", cursive;
    text-transform: capitalize;
    letter-spacing: 0.1em;
}
