@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@500&display=swap");

.root-container {
    background: #2d2d2d;
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
}
.app-container {
    width: 70%;
    height: 100%;
    min-height: 1024px;
    padding-bottom: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: auto;
}

.MuiBox-root {
    width: 100% !important;
}

#vertical-tabpanel-0 {
    width: 100% !important;
    min-width: 525px;
}

.MuiTabs-root {
    max-width: 125px !important;
    align-items: center;
    text-align: center;
    justify-content: center;
}

.MuiTab-wrapper {
    color: white;
    font-size: 1.5em;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-transform: capitalize;
    letter-spacing: 0.05em;
}
.movie-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.movie-container > * {
    padding: 20px 15px;
}

.movie-name-wrapper {
    min-width: 300px;
    width: 100%;
    color: rgb(8, 157, 243);
    font-size: 1em;
    margin-right: 15px;
    font-family: "Roboto Slab", serif;
    letter-spacing: 0.05em;
    text-transform: capitalize;
}
.movie-year-wrapper {
    width: 100%px;
    min-width: 60px;
    color: tomato;
    text-align: center;
}

#is-watched {
    width: 100%;
    min-width: 100px;
    text-align: center;
}

.watched {
    color: rgb(235, 63, 11);
    font-weight: bolder;
}
.not-watched {
    color: rgb(11, 235, 197);
}

.download-link {
    color: rgb(192, 26, 170);
    transition: 0.5s;
}

.download-link a {
    text-decoration: none;
    color: white;
}
.download-link:hover {
    transform: translateY(-4px);
    transition: 0.5s;
    filter: drop-shadow(2px 10px 5px rgb(17, 17, 17));
}

.random-comp {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}




.imdb-wrapper {
    color: rgb(235, 231, 0);
}
@media (max-width: 970px) {
    #vertical-tabpanel-0 {
        width: 100% !important;
        min-width: 400px !important;
    }
    .MuiBox-root {
        width: 95% !important;
    }

    .app-container {
        width: 98%;
    }

    .movie-name-wrapper {
        min-width: 250px;
    }
}
